.overlay {
  border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	color: #FFF;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	text-align: center;
	top: 0;
}

.box:hover .overlay {
	background-color: rgba(0, 0, 0, 0.6);
}

.overlay .content {
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	transition: 0.2s;
}

.box:hover .content {
	bottom: 15px;
}

.overlay .view-community {
	border: solid 1px #FFF;
	opacity: 0;
	margin: 20px auto;
	max-width: 130px;
	padding: 10px;
	transition: 0.2s;
	transition-property: bottom;
}

.box:hover .view-community {
	opacity: .6;
}

.view-community:hover {
	background-color: #000;
	color: #fff;
}

.overlay .heading {
	font-family: "Verlag A", "Verlag B", "Helvetica", "Arial", sans-serif;
	font-size: 1.8em;
	font-weight: 100;
  letter-spacing: 2px;
	line-height: 1.1em;
	margin-top: 100px auto;
	margin-bottom: 0.15em;
	text-align: center;
	text-shadow: 2px 0px 5px rgba(0,0,0,0.2);
}

.overlay .desc {
	font-size: 1em;
  letter-spacing: .1em;
	line-height: 1.2em;
	font-weight: 100;
	padding-left: 1.5em;
	padding-right: 1.5em;
	text-align: center;
}

